import React, { useState } from "react";
import "./App.css";

function App() {
  const [result, setResult] = useState("");
  const handleClick = (e) => {
    setResult(result.concat(e.target.name));
  };
  const clear = () => {
    setResult("");
  };
  const backspace = () => {
    setResult(result.slice(0, -1));
  };
  const calculate = () => {
    try {
      setResult(eval(result).toString());
    } catch (err) {
      setResult("Error");
    }
  };
  function turnLight() {
    document.getElementById("themes").style.color = "#101116";
    document.getElementById("theme").style.color = "#101116";
    document.getElementById("theme").style.backgroundColor = "#fff";
  }
  function turnDark() {
    document.getElementById("themes").style.color = "#fff";
    document.getElementById("theme").style.color = "#fff";
    document.getElementById("theme").style.backgroundColor = "#101116";
  }

  return (
    <>
      <div>
        <div id="theme" className="container">
          <div className="radio">
            <label htmlFor="light">Light</label>
            <input id="theme" onClick={turnLight} name="light" type="radio" />
            <label htmlFor="light">Dark</label>
            <input id="theme" onClick={turnDark} name="light" type="radio" />
            <label htmlFor="light"> &copy;Saad Ali </label>
          </div>
          <form>
            <input id="themes" type="text" value={result} />
          </form>
          <div className="keypad">
            <button onClick={clear} id="clear">
              Clear
            </button>
            <button onClick={backspace} id="backspace">
              C
            </button>
            <button name="/" onClick={handleClick}>
              &divide;
            </button>
            <button name="7" onClick={handleClick}>
              7
            </button>
            <button name="8" onClick={handleClick}>
              8
            </button>
            <button name="9" onClick={handleClick}>
              9
            </button>
            <button name="*" onClick={handleClick}>
              &times;
            </button>
            <button name="4" onClick={handleClick}>
              4
            </button>
            <button name="5" onClick={handleClick}>
              5
            </button>
            <button name="6" onClick={handleClick}>
              6
            </button>
            <button name="-" onClick={handleClick}>
              &ndash;
            </button>
            <button name="1" onClick={handleClick}>
              1
            </button>
            <button name="2" onClick={handleClick}>
              2
            </button>
            <button name="3" onClick={handleClick}>
              3
            </button>
            <button name="+" onClick={handleClick}>
              +
            </button>
            <button name="0" onClick={handleClick}>
              0
            </button>
            <button name="." onClick={handleClick}>
              .
            </button>
            <button onClick={calculate} id="result">
              =
            </button>
          </div>
        </div>
      </div>
    </>
  );
}

export default App;
